<template>
  <div class="personnal-detail-wrap">
    <div class="personnal-detail-wrap-title">
      {{title}}
      <span class="personnal-detail-wrap-title-date">{{testDate}}</span>
      <span v-if="isGrade" class="personnal-detail-wrap-title-score fr">
        得分：
        <span>{{score}}</span>
      </span>
    </div>
    <div class="personnal-detail-wrap-head">
      <el-checkbox
        class="fl"
        v-model="allChecked"
        @change="allCheck"
      >已选{{choosedNum}}题（共{{total}}题，错题总分{{errorScore}}分）</el-checkbox>
      <el-checkbox-group v-if="isGrade" class="fr" v-model="checkList">
        <el-checkbox :disabled="isAllCorrect" label="只看错题"></el-checkbox>
        <el-checkbox :disabled="isAllFalse" label="只看对题"></el-checkbox>
      </el-checkbox-group>
      <div class="clear"></div>
    </div>
    <div class="personnal-detail-wrap-container">
      <questions-card v-for="(c,i) in questions" :key="i" :card="c" @choose="chooseHandler"/>
      <el-button v-if="isTooMore" class="show-more" type="primary" size="mini">展开所有题目</el-button>
    </div>
  </div>
</template>
<script>
import questionsCard from "../../../components/personal/student/questionsCard.vue";
import http from "../../../utils/http.js";
export default {
  components: {
    questionsCard
  },
  data() {
    return {
      isGrade: true,
      id: "",
      title: "大学英语—随堂测试",
      score: 83,
      testDate: "2019-05-12 14:00",
      allChecked: false,
      checkList: ["只看错题"],
      isAllCorrect: false,
      isAllFalse: true,
      choosedNum: 0,
      total: 30,
      errorScore: 100,
      questions: [
        {
          id: "123",
          checked: false,
          title:
            "Both the secretary and the manager ______ agreed to attend the meeting.",
          choices: ["has", "were", "have", "will"],
          answer: "B",
          point: 3
        },
        {
          id: "123",
          checked: false,
          title:
            "Both the secretary and the manager ______ agreed to attend the meeting.",
          choices: ["has", "were", "have", "will"],
          answer: "B",
          point: 3
        },
        {
          id: "123",
          checked: false,
          title:
            "Both the secretary and the manager ______ agreed to attend the meeting.",
          choices: ["has", "were", "have", "will"],
          answer: "B",
          point: 3
        },
        {
          id: "123",
          checked: false,
          title:
            "Both the secretary and the manager ______ agreed to attend the meeting.",
          choices: ["has", "were", "have", "will"],
          answer: "B",
          point: 3
        },
        {
          id: "123",
          checked: false,
          title:
            "Both the secretary and the manager ______ agreed to attend the meeting.",
          choices: ["has", "were", "have", "will"],
          answer: "B",
          point: 3
        },
        {
          id: "123",
          checked: false,
          title:
            "Both the secretary and the manager ______ agreed to attend the meeting.",
          choices: ["has", "were", "have", "will"],
          answer: "B",
          point: 3
        },
