          checked: false,
          title:
            "Both the secretary and the manager ______ agreed to attend the meeting.",
          choices: ["has", "were", "have", "will"],
          answer: "B",
          point: 3
        },
        {
          id: "123",
          checked: false,
          title:
            "Both the secretary and the manager ______ agreed to attend the meeting.",
          choices: ["has", "were", "have", "will"],
          answer: "B",
          point: 3
        },
        {
          id: "123",
          checked: false,
          title:
            "Both the secretary and the manager ______ agreed to attend the meeting.",
          choices: ["has", "were", "have", "will"],
          answer: "B",
          point: 3
        },
        {
          id: "456",
          checked: false,
          title:
            "Both the secretary and the manager ______ agreed to attend the meeting.",
          choices: ["has", "were", "have", "will"],
          answer: "B",
          point: 3
        }
      ],
      isTooMore: false,
      selected: []
    };
  },
  created() {
    this.initData();
  },
  watch: {
    $route(to, from) {
      this.initData();
    }
  },
  methods: {
    initData() {
      this.isTooMore = this.questions.length > http.pageSize;
      let currentPosi = this.$route.path.split("/");
      this.id = currentPosi[5];
      this.isGrade = currentPosi[3] == "grade";
    },
    allCheck(val) {
      this.questions.forEach(element => {
        this.$set(element, "checked", val);
        this.chooseHandler({ choose: val, id: element.id });
      });
    },
    chooseHandler(val) {
      if (val.choose) {
        this.selected.push(val.id);
      } else {
        let index = this.selected.indexOf(val.id);
        if (index > -1) {
          this.selected.splice(index, 1);
        }
      }
      this.checkAllHandler();
    },
    checkAllHandler() {
      this.allChecked = this.selected.length == this.questions.length;
    }
  }
};
</script>
<style lang="less" scoped>
.personnal-detail-wrap {
  &-title {
    &-date {
      color: #a0a0a0;
      font-size: 0.8rem;
      margin-left: 18px;
    }
    &-score {
      margin-top: -1.2rem;
      span {
        color: #48a8fc;
        font-size: 2.33rem;
      }
    }
  }
  &-head {
    border-bottom: 1px solid #e8e8e8;
    .fl {
      color: #a0a0a0;
    }
  }
  &-container {
    text-align: left;
    .show-more {
      margin-top: 25px;
      margin-left: 30px;
    }
  }
}
</style>



