<template>
  <div class="questions-card">
    <div class="questions-card-check fl">
      <el-checkbox v-model="card.checked" @change="changeHandler"></el-checkbox>
    </div>
    <div class="questions-card-main fr">
      <div class="questions-card-main-box">
        <i class="iconfont icon-biaoji blue-text"></i>
        <div class="questions-card-main-box-question">{{card.title}}</div>
        <ol type="A">
          <li v-for="o in card.choices" :key="o">{{o}}</li>
        </ol>
      </div>
      <div class="questions-card-main-bottom">
        <span class="fl">
          我的作答：
          <span class="red-text">{{card.answer}}</span>
        </span>
        <span class="fl">
          本题分值：
          <span class="blue-text">{{card.point}}</span>&nbsp;分
        </span>
        <el-button class="fr" type="primary" size="mini">标准答案与解析</el-button>
      </div>
    </div>
    <div class="clear"></div>
  </div>
</template>
<script>
export default {
  props: {
    card: Object
  },
  methods: {
    changeHandler(val) {
      this.$emit("choose", { choose: val, id: this.card.id });
    }
  }
};
</script>
<style lang="less" scoped>
.questions-card {
  width: 100%;
  height: 320px;
  margin-top: 20px;
  &-check {
    width: 30px;
    text-align: left;
  }
  &-main {
    width: calc(100% - 30px);
    height: 100%;
    border: 1px solid #e2e2e2;
    &-box {
      width: 100%;
      height: calc(100% - 40px);
      position: relative;
      padding: 16px;
      text-align: left;
      font-weight: 530;
      .iconfont {
        position: absolute;
        right: 16px;
        top: 16px;
        font-size: 1.52rem;
        cursor: pointer;
      }
      ol {
        margin-top: 12px;
      }
    }
    &-bottom {
      width: 100%;
      height: 40px;
      line-height: 40px;
      border-top: 1px solid #e2e2e2;
      padding: 0 20px;
      button {
        margin-top: 6px;
      }
      .fl {
        font-size: 0.9rem;
        margin-right: 2.33rem;
        span {
          font-size: 1.25rem;
          font-weight: 550;
        }
      }
    }
  }
}
</style>



